.App {
    text-align: center;
}
.slide{
    height:150px;
    z-index: 0 !important;
    position: relative;
    margin: 0 30px;
    /* border:1px solid black; */
}
.slider{
    position: relative;
    margin: 0px 10%;
}
.slider div{
    margin-right: 2px;
    color:white;
    margin-top: 0.2px;
    z-index:0 !important;
}
.box{
    background-size: cover;
    height:75px;
    width:100%;
    background-repeat: no-repeat;
    /* border: 1px solid black; */
}
.multi{
    display: flex;
    flex-direction: column;
}
.img{
    /* background-image: url(./media/trial.JPG); */
    background-size: contain;
    background-repeat: no-repeat;
    background-color: aquamarine;
}
#Layer_1{
    transform: scale(0.9);
    position: relative;
    top:0;
    left:0;
}
.panel{
    position:relative;
    margin:auto;
    height:200px;
    border:1px solid black;
    width:100%;
    background-color:white;
    border-radius:20px;
    padding-top:50px;
    overflow:hidden;
}
.hover{
    height:10px;
    transform-origin: center;
    width:200%;
    top:0;
    background-color: #3f51b5;
    position: absolute;
    z-index: 0;
    transition: height 0.3s;
}
.panel:hover > .hover{
    height: 250px;
}
.panel:hover{
    color: white;
}

@media(max-width:700px){
    .panel{
        width:80%;
    }
}
