.App {
    text-align: center;
}
.slide{
    height:150px;
    z-index: 0 !important;
    position: relative;
    margin: 0 30px;
    /* border:1px solid black; */
}
.slider{
    position: relative;
    margin: 0px 10%;
}
.slider div{
    margin-right: 2px;
    color:white;
    margin-top: 0.2px;
    z-index:0 !important;
}
.box{
    background-size: cover;
    height:75px;
    width:100%;
    background-repeat: no-repeat;
    /* border: 1px solid black; */
}
.multi{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.img{
    /* background-image: url(./media/trial.JPG); */
    background-size: contain;
    background-repeat: no-repeat;
    background-color: aquamarine;
}
#Layer_1{
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    position: relative;
    top:0;
    left:0;
}
.panel{
    position:relative;
    margin:auto;
    height:200px;
    border:1px solid black;
    width:100%;
    background-color:white;
    border-radius:20px;
    padding-top:50px;
    overflow:hidden;
}
.hover{
    height:10px;
    -webkit-transform-origin: center;
            transform-origin: center;
    width:200%;
    top:0;
    background-color: #3f51b5;
    position: absolute;
    z-index: 0;
    transition: height 0.3s;
}
.panel:hover > .hover{
    height: 250px;
}
.panel:hover{
    color: white;
}

@media(max-width:700px){
    .panel{
        width:80%;
    }
}

.events{
    font-family: "Avenir Light";
    background-color:white;
    padding-bottom:100px;
    margin-top: -21px;
}
.grid{
    margin-top:70px;
}
.odd{
    -webkit-transform:translateX(80px);
            transform:translateX(80px);
    transition:-webkit-transform 0.3s ease-in-out;
    transition:transform 0.3s ease-in-out;
    transition:transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.even{
    -webkit-transform:translateX(-80px);
            transform:translateX(-80px);
    transition:-webkit-transform 0.3s ease-in-out;
    transition:transform 0.3s ease-in-out;
    transition:transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.grid:hover>.odd,.grid:hover>.even{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.label{
    height:50%;
    width:100%;
    background-color: aquamarine;
    
}

